














































import { Component, Vue } from "vue-property-decorator";

@Component({})
export default class LoginView extends Vue {
  id = "";
  pw = "";

  isLoading = false;

  async logInClicked(): Promise<void> {

    //this.$recaptcha();

    this.isLoading = true;

    const options = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        identifier: this.id,
        password: this.pw,
      }),
    };

    const response = await fetch(
      "https://strapi.kreimben.com/auth/local",
      options
    );
    const json = await response.json();

    console.log(`response is : ${JSON.stringify(json)}`);

    if (json.jwt !== null || json.jwt !== undefined) {
        sessionStorage.setItem("strapi_token", json.jwt);
        //Vue.$cookies.set("strapi_token", `${json.jwt}`);
    } else if (json.statusCode === 400) {
        alert(`Login fail`);
        this.isLoading = false;
        return;
    }

    this.isLoading = false;

    alert(`token saved: ${sessionStorage.getItem("strapi_token")}`)
  }
}
